#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}

.Legend {
  z-index: 1;
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: white;
  width: 250px;
}

.colourtag {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  vertical-align: middle;
  margin: 10px;
  float: left;
  clear: right;
}

.legend {
  height: 40px;
  width: 100%;
  display: block;
}

.legend p {
  float: left;
  width: 70%;
  margin: 10px;
}

.timeslide {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 95%;
}

.timeslide input {
  width: 90%;
}

.Capture {
  z-index: 1;
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: white;
  width: 300px;
}

.capture-button {
  width: 100px;
  padding: 10px;
  background-color: lightgray;
  margin: 0 auto;
}
.capture-button:hover {
  background-color: gray;
  cursor: pointer;
}
.capture-field {
  font-size: 16px;
  font-weight: bold;
  margin: 20px;
  display: block;
  width: 240px;
  padding: 4px 8px;
  margin-bottom: 8px;
  border-radius: 3px;
  border: 1px solid #888;
  font-size: 14px;
}

.capture-textarea {
  font-size: 16px;
  font-weight: bold;
  margin: 20px;
  display: block;
  width: 240px;
  height: 100px;
  padding: 4px 8px;
  margin-bottom: 8px;
  border-radius: 3px;
  border: 1px solid #888;
}



